<template>
  <div class="main">
    
    <header>
      <h1>CLEAN CHECK</h1>
    </header>

    <section id="content">
      <p>Please select the problem with your seat from the list below:</p>

      <div @click="select(`opt1`)" :class="isSelected('opt1')"><strong><a href="#">Not cleaned last 48 hours</a></strong></div>
      <div @click="select(`opt2`)" :class="isSelected('opt2')"><strong><a href="#">Spill on my seat</a></strong></div>
      <div @click="select(`opt3`)" :class="isSelected('opt3')"><strong><a href="#">Seat is broken</a></strong></div>
      <div @click="select(`opt4`)" :class="isSelected('opt4')"><strong><a href="#">Something else</a></strong></div>

      <p>&nbsp;</p>
      <div class="button small" @click="complete"><strong><a href="#">SUBMIT</a></strong></div>
    </section>

  </div>
</template>

<script>
export default {
  name: 'Problem',
  data () {
    return {
      selected: null,
    }
  },
  methods: {
    complete() {
      this.$router.push({name: 'Confirmation'})
    },
    select (id) {
      if(this.selected !== id ){
        this.selected = id
      }
    },
    isSelected (id) {
      if (id === this.selected) {
        return 'small button3-selected'
      } else {
        return 'button3 small'
      }
    }
  }
}
</script>

