const state = {
  overlayOn: false,
  overlayView: null
}

const mutations = {
  mutateOverlay (state, _overlayOn) {
    state.overlayOn = _overlayOn
  },
  mutateOverlayView (state, _view) {
    state.overlayView = _view
  }
}

const actions = {
  setOverlay ({commit}, _bool) {
    commit('mutateOverlay', _bool)
  },
  setOverlayView ({commit}, _view) {
    commit('mutateOverlayView', _view)
  }
}

const getters = {
  overlayOn: state => { return state.overlayOn },
  overlayView: state => { return state.overlayView }
}

export default {
  state,
  actions,
  mutations,
  getters
}
