import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/clean',
    name: 'Cleaned',
    component: () => import('@/components/employee/Cleaned')
  },
  {
    path: '/confirmation',
    name: 'Confirmation',
    component: () => import('@/components/client/Confirmation')
  },
  {
    path: '/problem',
    name: 'Problem',
    component: () => import('@/components/client/Problem')
  },
  {
    path: '/:type',
    name: 'homeType',
    component: Home
  },
  {
    path: '/',
    name: 'Home',
    component: Home
  }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
