<script>
import moment from 'moment'
import {api} from '@/utils/api'
import { mapActions } from 'vuex'

export default {
  name: 'Patron',
  data () {
    return {
      lastCleaned: null,
      timeDiff: null,
      timeCleaned: null
    }
  },
  mounted () {
    api.getRaw('gp/test').then( response => {
      this.lastCleaned = moment(response.data.lastCleaned).format("M/DD/YYYY")
      this.timeCleaned = moment(response.data.lastCleaned).format("hh:mm A")
      this.timeDiff = `${response.data.timeDiff}`
    }).catch( err => {
      console.log(err)
    })
  },
  methods: {
    ...mapActions([
      'setOverlay',
      'setOverlayView'
    ]),
    report() {
      this.setOverlayView('overlay-problem')
      this.setOverlay(true)
      //this.$router.push({name: 'Problem'})
    },
    confirm() {
      this.setOverlayView('overlay-good')
      this.setOverlay(true)
    }
  }
}
</script>

