<script>
export default {
  name: 'Certify',
  data () {
    return {
      selected: null,
    }
  },
  methods: {
    complete() {
      this.$router.push({name: 'Confirmation'})
    },
    select (id) {
      if(this.selected !== id ){
        this.selected = id
      }
    },
    isSelected (id) {
      if (id === this.selected) {
        return 'small button3-selected'
      } else {
        return 'button3 small'
      }
    }
  }
}
</script>

